import React from 'react';

// render a simple SVG making up a right facing arrow
const Arrow = () => {
    return(
        <svg xmlns="http://www.w3.org/2000/svg" width="100" height="100" viewBox="0 0 26.458332 26.45833"><path d="M3.969 0l18.52 13.23S3.97 16.007 3.97 0z"/><path d="M3.969 26.458l18.52-13.229S3.97 10.45 3.97 26.46z"/></svg>
    );
}

export default Arrow;



